







































































































/* eslint-disable @typescript-eslint/no-explicit-any */
import { Getter, Mutation, Action } from 'vuex-class'
import animations from 'create-keyframe-animation'
import { prefixStyle } from '@/common/ts/dom'
import ProgressBar from '@/components/progress-bar/progress-bar.vue'
import ProgressCircle from '@/components/progress-circle/progress-circle.vue'
import Lyric from 'lyric-parser'
import { playMode } from '@/common/ts/config'
import Scroll from '@/components/scroll/scroll.vue'
import Confirm from '@/components/confirm/confirm.vue'
import { shuffle } from '@/common/ts/util'
import { PlayerMixin } from '@/common/ts/mixin'
import Component from 'vue-class-component'
import Playlist from '@/components/playlist/playlist.vue'
import { Song } from '@/common/ts/song'
import { Watch } from 'vue-property-decorator'
const transform = prefixStyle('transform')
const transitionDuration = prefixStyle('transitionDuration')
interface Touch{
    moved: boolean;
    startX: number;
    startY: number;
    percent: number;
    initiated: boolean;
}
@Component({
  components: {
    ProgressBar,
    ProgressCircle,
    Scroll,
    Playlist,
    Confirm
  }
})
export default class Play extends PlayerMixin {
    private songReady = false;
    private currentTime = 0;
    private radius = 32;
    private currentLyric: any = null;
    private currentLineNum = 0;
    private currentShow = 'cd'
    private playingLyric = ''
    private touch!: Touch;
    private timer: any = null;
    get cdCls () {
      return this.playing ? 'play' : 'play pause'
    }

    get playIcon () {
      return this.playing ? 'icon-pause' : 'icon-play'
    }

    get iconMode () {
      return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' : 'icon-random'
    }

    get miniIcon () {
      return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
    }

    get disableCls () {
      return this.songReady ? '' : 'disable'
    }

    get percent () {
      return this.currentTime / this.currentSong.duration
    }

    @Getter('currentIndex') currentIndex!: number;
    @Getter('fullScreen') fullScreen!: boolean;
    @Getter('playing') playing!: boolean;
    @Getter('playlist') playlist!: Song[];
    @Getter('sequenceList') sequenceList!: Song[];
    @Getter('currentSong') currentSong!: Song;
    @Getter('mode') mode!: number;
    private created () {
      this.touch = {
        moved: false,
        startX: 0,
        startY: 0,
        percent: 0,
        initiated: false
      }
    }

    private cancel () {
      const time = 3000
      const offsetWidth = -window.innerWidth
      const opacity = 0;
      ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transform] = `translate3d(${offsetWidth}px,0,0)`;
      ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transitionDuration] = `${time}ms`;
      (this.$refs.middleL as HTMLElement).style.opacity = opacity + '';
      (this.$refs.middleL as HTMLElement).style[transitionDuration] = `${time}ms`
      this.currentShow = 'lyric'
      return null
    }

    private confirm () {
      this.$router.go(0)
    }

    private showPlaylist () {
      (this.$refs.playlist as any).show()
    }

    private moveSong (time: number) {
      (this.$refs.audio as HTMLAudioElement).currentTime = time / 1000
      if (this.currentLyric) {
        this.currentLyric.seek(time)
      }
    }

    private back () {
      this.setFullScreen(false)
    }

    private open () {
      this.setFullScreen(true)
    }

    private enter (el: HTMLElement, done: Function) {
      const { x, y, scale } = this._getPosAndScale()
      const animation = {
        0: {
          transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
        },
        60: {
          transform: 'translate3d(0,0,0) scale(1.1)'
        },
        100: {
          transform: 'translate3d(0,0,0) scale(1)'
        }
      }
      animations.registerAnimation({
        name: 'move',
        animation,
        presets: {
          duration: 2000,
          easing: 'linear'
        }
      })
      animations.runAnimation(this.$refs.cdWrapper, 'move', done)
    }

    private afterEnter () {
      animations.unregisterAnimation('move');
      (this.$refs.cdWrapper as HTMLElement).style.animation = ''
    }

    private leave (el: HTMLElement, done: any) {
      (this.$refs.cdWrapper as HTMLElement).style.transition = 'all 2s'
      const { x, y, scale } = this._getPosAndScale();
      (this.$refs.cdWrapper as HTMLElement).style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`;
      (this.$refs.cdWrapper as HTMLElement).addEventListener('transitionend', done)
    }

    private afterLeave () {
      (this.$refs.cdWrapper as HTMLElement).style.transition = '';
      (this.$refs.cdWrapper as HTMLElement).style[transform] = ''
    }

    private togglePlaying () {
      if (!this.songReady) {
        return
      }
      this.setPlayingState(!this.playing)
      if (this.currentLyric) {
        this.currentLyric.togglePlay()
      }
    }

    private end () {
      if (this.mode === playMode.loop) {
        this.loop()
      } else {
        this.next()
      }
    }

    private loop () {
      (this.$refs.audio as HTMLAudioElement).currentTime = 0;
      (this.$refs.audio as HTMLAudioElement).play()
      this.setPlayingState(true)
      if (this.currentLyric) {
        this.currentLyric.seek(0)
      }
    }

    private next () {
      if (!this.songReady) {
        return
      }
      if (this.playlist.length === 1) {
        this.loop()
        return
      } else {
        let index = this.currentIndex + 1
        if (index === this.playlist.length) {
          index = 0
        }
        this.setCurrentIndex(index)
        if (!this.playing) {
          this.togglePlaying()
        }
      }
      this.songReady = false
    }

    private prev () {
      if (!this.songReady) {
        return
      }
      if (this.playlist.length === 1) {
        this.loop()
        return
      } else {
        let index = this.currentIndex - 1
        if (index === -1) {
          index = this.playlist.length - 1
        }
        this.setCurrentIndex(index)
        if (!this.playing) {
          this.togglePlaying()
        }
      }
      this.songReady = false
    }

    private ready () {
      this.songReady = true
      this.savePlayHistory(this.currentSong)
    }

    private error () {
      if (this.currentSong.url === '') { return }
      this.songReady = true;
      (this.$refs.confirm as any).show()
    }

    private updateTime (e: any) {
      this.currentTime = e.target.currentTime
    }

    private format (interval: number|undefined) {
    //   console.log(interval)
      interval = interval || 0
      const minute = ~~(interval / 60) || 0
      const second = this._pad(~~(interval % 60))
      //   console.log(minute, second)
      return `${minute}:${second}`
    }

    private onProgressBarChange (percent: number) {
      const currentTime = this.currentSong.duration * percent;
      (this.$refs.audio as HTMLAudioElement).currentTime = currentTime
      if (!this.playing) {
        this.togglePlaying()
      }
      if (this.currentLyric) {
        this.currentLyric.seek(currentTime * 1000)
      }
    }

    private getLyric () {
      this.currentSong.getLyric().then((lyric) => {
        if (this.currentSong.lyric !== lyric) {
          return
        }
        this.currentLyric = new Lyric(lyric, this.handleLyric)
        // console.log(this.currentLyric)
        if (this.playing) {
          this.currentLyric.play()
        }
      }).catch(() => {
        this.currentLyric = null
        this.playingLyric = ''
        this.currentLineNum = 0
      })
    }

    private handleLyric ({ lineNum, txt }: {lineNum: number; txt: string}) {
      this.currentLineNum = lineNum
      if (lineNum > 5) {
        const lineEl = (this.$refs.lyricLine as HTMLElement[])[lineNum - 5];
        (this.$refs.lyricList as any).scrollToElement(lineEl, 1000)
      } else {
        (this.$refs.lyricList as any).scrollTo(0, 0, 1000)
      }
      this.playingLyric = txt
    }

    private middleTouchStart (e: any) {
      this.touch.initiated = true
      this.touch.moved = false
      const touch = e.touches[0]
      this.touch.startX = touch.pageX
      this.touch.startY = touch.pageY
    }

    private middleTouchMove (e: any) {
      if (!this.touch.initiated) {
        return
      }
      const touch = e.touches[0]
      const deltaX = touch.pageX - this.touch.startX
      const deltaY = touch.pageY - this.touch.startY
      if (Math.abs(deltaY) > Math.abs(deltaX)) {
        return
      }
      if (!this.touch.moved) {
        this.touch.moved = true
      }
      const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
      const offsetWidth = Math.min(0, Math.max(-window.innerWidth, left + deltaX))
      this.touch.percent = Math.abs(offsetWidth / window.innerWidth)
      // ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transform] = `translate3d(${offsetWidth}px,0,0)`;
      // ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transitionDuration] = 0 + '';
      // (this.$refs.middleL as HTMLElement).style.opacity = 1 - this.touch.percent + '';
      // (this.$refs.middleL as HTMLElement).style[transitionDuration] = 0 + ''
    }

    middleTouchEnd () {
      if (!this.touch.moved) {
        return
      }
      let offsetWidth
      let opacity
      if (this.currentShow === 'cd') {
        if (this.touch.percent > 0.1) {
          offsetWidth = -window.innerWidth
          opacity = 0
          this.currentShow = 'lyric'
        } else {
          offsetWidth = 0
          opacity = 1
        }
      } else {
        if (this.touch.percent < 0.9) {
          offsetWidth = 0
          this.currentShow = 'cd'
          opacity = 1
        } else {
          offsetWidth = -window.innerWidth
          opacity = 0
        }
      }
      const time = 3000;
      ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transform] = `translate3d(${offsetWidth}px,0,0)`;
      ((this.$refs.lyricList as Vue).$el as HTMLElement).style[transitionDuration] = `${time}ms`;
      (this.$refs.middleL as HTMLElement).style.opacity = opacity + '';
      (this.$refs.middleL as HTMLElement).style[transitionDuration] = `${time}ms`
      this.touch.initiated = false
    }

    private _pad (num: number, n = 2) {
      let len = num.toString().length
      let newNum = num + ''
      while (len < n) {
        newNum = '0' + newNum
        len++
      }
      return newNum
    }

    private _getPosAndScale () {
      const targetWidth = 40
      const paddingLeft = 40
      const paddingBottom = 30
      const paddingTop = 80
      const width = window.innerWidth * 0.8
      const scale = targetWidth / width
      const x = -(window.innerWidth / 2 - paddingLeft)
      const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
      return {
        x,
        y,
        scale
      }
    }

    @Mutation('SET_FULL_SCREEN') setFullScreen!: Function;
    @Mutation('SET_CURRENT_INDEX') setCurrentIndex!: Function;
    @Mutation('SET_PLAYING_STATE') setPlayingState!: Function;
    @Mutation('SET_PLAY_MODE') setPlayMode!: Function;
    @Mutation('SET_PLAYLIST') setPlaylist!: Function;
    @Action('savePlayHistory') savePlayHistory!: Function;
    @Watch('currentSong') currentSongChanged (newSong: Song, oldSong: Song) {
      // console.log(newSong)
      if (!newSong.id) {
        return
      }
      if (newSong.id === oldSong.id) {
        return
      }
      if (this.currentLyric) {
        this.currentLyric.stop()
        this.currentTime = 0
        this.playingLyric = ''
        this.currentLineNum = 0
      }
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        (this.$refs.audio as HTMLAudioElement).play()
        this.getLyric()
      }, 1000)
    }

    @Watch('playing') playingChanged (newPlaying: boolean) {
      const audio = this.$refs.audio as HTMLAudioElement
      this.$nextTick(() => {
        newPlaying ? audio.play() : audio.pause()
      })
    }

  @Watch('fullScreen') fullScreenChanged (newVal: boolean) {
      if (newVal) {
        setTimeout(() => {
          // this.$refs.lyricList.refresh()
        }, 20)
      }
    }
}
