













import { Vue, Component, Prop, Emit } from 'vue-property-decorator'
@Component({})
export default class SearchList extends Vue {
    @Prop({ type: Array, default: function () { return [] } }) searches!: string[];
    @Emit('select') selectEmit (item: string) { return item }
    @Emit('delete') deleteEmit (item: string) { return item }
    private selectItem (item: string) {
      this.selectEmit(item)
    }

    private deleteOne (item: string) {
      this.deleteEmit(item)
    }
}
