










































import { Action } from 'vuex-class'
import { playMode } from '@/common/ts/config'
import Scroll from '@/components/scroll/scroll.vue'
import Confirm from '@/components/confirm/confirm.vue'
import AddSong from '@/components/add-song/add-song.vue'
import { PlayerMixin } from '@/common/ts/mixin'
import { Vue, Component, Watch } from 'vue-property-decorator'
import { Song } from '@/common/ts/song'

@Component({
  components: {
    Scroll,
    Confirm,
    AddSong
  }
})
export default class Playlist extends PlayerMixin {
//   mixins: [playerMixin],
private showFlag = false;
private refreshDelay = 120;
get modeText () {
  return this.mode === playMode.sequence ? '顺序播放' : this.mode === playMode.random ? '随机播放' : '单曲循环'
}

private show () {
  this.showFlag = true
  setTimeout(() => {
    (this.$refs.listContent as any).refresh()
    this.scrollToCurrent(this.currentSong)
  }, 20)
}

private hide () {
  this.showFlag = false
}

private showConfirm () {
  (this.$refs.confirm as any).show()
}

private confirmClear () {
  this.deleteSongList()
  this.hide()
}

private getCurrentIcon (item: Song) {
  if (this.currentSong.id === item.id) {
    return 'icon-play'
  }
  return ''
}

private selectItem (item: Song, index: number) {
  if (this.mode === playMode.random) {
    index = this.playlist.findIndex((song) => {
      return song.id === item.id
    })
  }
  this.setCurrentIndex(index)
  this.setPlayingState(true)
}

private scrollToCurrent (current: Song) {
  const index: number = this.sequenceList.findIndex((song) => {
    return current.id === song.id
  });
  (this.$refs.listContent as any).scrollToElement((this.$refs.list as Vue).$el.children[index], 300)
}

private deleteOne (item: Song) {
  this.deleteSong(item)
  if (!this.playlist.length) {
    this.hide()
  }
}

private addSong () {
  (this.$refs.addSong as any).show()
}

    @Action('deleteSong') deleteSong!: Function;
    @Action('deleteSongList') deleteSongList!: Function;
    @Watch('currentSong') currentSongChanged (newSong: Song, oldSong: Song) {
      if (!this.showFlag || newSong.id === oldSong.id) {
        return
      }
      setTimeout(() => {
        this.scrollToCurrent(newSong)
      }, 20)
    }
}
