










































import SearchBox from '@/components/search-box/search-box.vue'
import SongList from '@/components/song-list/song-list.vue'
import SearchList from '@/components/search-list/search-list.vue'
import Scroll from '@/components/scroll/scroll.vue'
import Switches from '@/components/switches/switches.vue'
import TopTip from '@/components/top-tip/top-tip.vue'
import Suggest from '@/components/suggest/suggest.vue'
import { SearchMixin } from '@/common/ts/mixin'
import { Getter, Action } from 'vuex-class'
import { Song } from '@/common/ts/song'
import Component from 'vue-class-component'

@Component({
  components: {
    SearchBox,
    SongList,
    SearchList,
    Scroll,
    Switches,
    TopTip,
    Suggest
  }
})
export default class AddSong extends SearchMixin {
    @Getter('playHistory') playHistory!: Song[];
    private showFlag = false;
    private showSinger = false;
    private currentIndex = 0;
    private songs: Song[] = []
    private switches = [
      {
        name: '最近播放'
      },
      {
        name: '搜索历史'
      }
    ]

    private show () {
      this.showFlag = true
      setTimeout(() => {
        if (this.currentIndex === 0) {
          (this.$refs.songList as any).refresh()
        } else {
          (this.$refs.searchList as any).refresh()
        }
      }, 20)
    }

    private hide () {
      this.showFlag = false
    }

    private selectSong (song: Song, index: number) {
      if (index !== 0) {
        this.insertSong(new Song(song));
        (this.$refs.topTip as any).show()
      }
    }

    private selectSuggest () {
      (this.$refs.topTip as any).show()
      this.saveSearch()
    }

    private switchItem (index: number) {
      this.currentIndex = index
    }

    @Action('insertSong') insertSong!: Function;
}
